.hero {
    position: relative;
    margin-top: -80px;
    width: 100%;
    height: 100vh;
}

.videos {
    width: 100%;
    height: 100vh;
    -o-object-fit: cover;
    object-fit: cover;
    background: #233a34;
}

.content {
    position: absolute;
    max-width: 50%;
    max-height: 60%;
    top: 50%;
    color: #fff;
    transform: translate(50%, -50%);
    text-align: center;
}

.content h1 {
    margin-top: 0;
    font-size: 5rem;
}

.btn a {
    color: #fff;
    text-decoration: none;
    display: inline-block;
    padding: 15px 40px;
    background-color: #01bf71;
    border: none;
    border-radius: 5px;
    transition: all 0.3s ease-in-out
}

.btn a:hover {
    background-color: #233a34;
}