input {
    margin: 10px 0;
    font-size: 16px;
}

input[type="text"] {
    border: 2px solid #2f3640;
    height: 45px;
    padding: 10px;
    width: 100%;
    border-radius: 15px;
    outline: none;
}

input[type="text"]:focus {
    box-shadow: 0 0 2pt 1pt #2f3640;
}

textarea {
    border: 2px solid #2f3640;
    padding: 10px;
    width: 100%;
    border-radius: 15px;
    outline: none;
}

textarea:focus {
    box-shadow: 0 0 2pt 1pt #2f3640;
}

input[type="submit"] {
    padding: 10px 25px;
    border-radius: 10px;
    border: 2px solid #2f3640;
    font-weight: bold;
    transition: all 0.3s ease-in-out;
}

input[type="submit"]:hover {
    background-color: #2f3640;
    color: white;
    cursor: pointer;
}