.service-section {
    background-color: #000;
    text-align: center;
    padding: 100px 0;
}

.service-wrapper {
    max-width: 1100px;
    margin: 0 auto;
}

.service-wrapper h1 {
    color: #fff;
    font-size: 4rem;
    margin-top: 0;
}

.services {
    display: flex;
    justify-content: space-between;
}

.service {
    background-color: #fff;
    margin: 10px;
    border-radius: 10px;
    transition: all 0.2s ease-in-out;
    padding: 10px;
}

.service h2 {
    font-size: 1.8rem;
}

.service p {
    font-size: 1.8rem;
}

.service:hover {
    transform: scale(1.05);
    cursor: pointer;
}

.service figure {
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 100px;
    height: 100px
}

.service img {
    height: 100%;
    width: 100%;
}