.footer-section {
    background-color: #101522;
    padding: 30px 0;
}

.footer-wrapper {
    color: #fff;
    max-width: 1100px;
    margin: 0 auto;
}

.footer-section li {
    list-style: none;
}

.first-footer {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}

.first-footer .links {
    font-size: 1.5rem;
}

.footer-section a {
    color: #fff;
    text-decoration: none;
    transition: all 0.2s ease-in;
}

.footer-section a:hover {
    color: #01bf71
}

.first-footer .links {
    padding-left: 0;
}

.links a {
    line-height: 1.5;
}

.second-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 50px;
}

.second-footer h2 {
    cursor: pointer;
    font-size: 3rem;
}

.second-footer p {
    font-size: 1.5rem;
}

.social-icons {
    width: 250px;
    display: flex;
    justify-content: space-between;
}

.social-icons a {
    font-size: 3rem;
}