.article-link {
    text-decoration: none;
}

.article-link .article {
    background-color: #01bf71;
    color: #fff;
    padding: 10px;
    border-radius: 10px;
    margin-top: 10px;
    transition: all 0.3s ease-out;
    cursor: pointer;
    margin: 10px;
}

.related-article-width {
    display: inline-block;
    width: 30%;
    height: 150px;
    font-size: 1.5rem;
    overflow: hidden;
    text-align: center;
}

.article-link .article:hover {
    transform: scale(1.05);
}