.nav-header {
    height: 80px;
    font-size: 2rem;
    color: #fff;
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    transition: all 0.5s ease-in;
}

.nav-wrapper {
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

@media screen and (max-width:780px) {
    .nav-wrapper {
        max-width: 780px;
    }
}

@media screen and (max-width:480px) {
    .nav-wrapper {
        display: none;
    }
}

.bg-color {
    background-color: #000;
}

.trans {
    background-color: transparent;
}

.nav-heading h1 {
    margin: 0;
    font-weight: bold;
}

.nav-heading {
    cursor: pointer;
    padding: 5px 5px;
}