.info-normal-section {
    display: flex;
    justify-content: space-between;
}

.info-section {
    max-width: 1100px;
    padding: 100px 0;
    margin: 0 auto;
}

.info-section-reverse {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
}

.bg {
    background-color: #000;
    color: #fff;
}

.info-section figure {
    padding: 0;
    margin: 0;
    max-width: 500px;
    height: 100%;
}

.info-section img {
    width: 100%;
}

.info h1 {
    font-size: 6rem;
}

p.top {
    color: #01bf71;
}