.nav ul {
    display: flex;
    list-style: none;
    align-items: center;
}

.nav li {
    cursor: pointer;
    padding: 10px 15px;
    /* margin-left: 10px; */
    color: #fff;
    text-decoration: none;
    transition: all 0.3s linear;
    margin-left: 25px;
}

.nav li:hover {
    border-bottom: 5px solid #fff;
}

.nav li:active {
    border-bottom: 5px solid #fff;
}